body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(0, 0, 0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg {
  background: #232526; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #414345,
    #232526
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #414345,
    #232526
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.wrapper {
  max-width: 800px;
  margin: 0 auto;
  height: 100vh;
}

.innerwrapper {
  background: #f4fcff;
  padding: 45px;
  box-shadow: 0px 0px 30px 5px #00000082;
  border-radius: 5px;
}

.heading {
  background: #e0eafc; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #cfdef3,
    #e0eafc
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #cfdef3,
    #e0eafc
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  padding: 20px 25px;
}

.heading h2 {
  font-size: 30px;
  padding-bottom: 8px;
  font-weight: 900;
}

.subtext {
  font-size: 13px;
  line-height: 18px;
  color: #333;
}

.calculate-body {
  padding: 10px 35px;
  margin-top: 25px;
  box-sizing: border-box;
}

h3 {
  font-size: 26px;
}

.inputDate {
  border: 1px solid #00000021;
  outline: 0;
  padding: 8px 12px;
  border-radius: 5px;
  box-sizing: border-box;
  margin: 10px 0 0;
}

.button-container {
  margin: 15px 0 0;
}

.btn {
  padding: 8px 14px;
  font-size: 14px;
  outline: 0;
  border-radius: 5px;
  border: 1px;
  cursor: pointer;
}

.blue-btn-grad {
  background-image: linear-gradient(
    to right,
    #77a1d3 0%,
    #79cbca 51%,
    #77a1d3 100%
  );
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  text-transform: uppercase;
}

.blue-btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.red-btn-grad {
  background-image: linear-gradient(
    to right,
    #e52d27 0%,
    #b31217 51%,
    #e52d27 100%
  );
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  text-transform: uppercase;
}

.red-btn-grad:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.btn:nth-of-type(1) {
  margin-right: 10px; /* Adjust the margin as needed */
}

input[type="date"] {
  padding: 10px;
  border: 2px solid #00000026;
  border-radius: 5px;
  background-color: #d0dff4;
  color: #333;
  outline: none;
  width: 100%;
  max-width: 300px;
  box-sizing: border-box;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input[type="date"]:hover {
  border-color: #070707; /* Darker green on hover */
}

input[type="date"]:focus {
  border-color: #000000;
  box-shadow: 0 0 5px rgba(51, 58, 52, 0.5); /* Add a glow effect on focus */
}

h4 {
  font-size: 20px;
}

#age-is {
  font-size: 80px;
}

#answer {
  padding: 30px;
  background: #ede574; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #e1f5c4,
    #ede574
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #e1f5c4,
    #ede574
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  width: 250px;
  height: 150px;
  text-align: center;
  box-sizing: border-box;
  border-radius: 5px;
}

@media screen and (max-width: 450px) {
  .left-col {
    margin: 10px 0 45px;
    width: 100%;
  }
  .innerwrapper {
    padding: 0;
  }
  .right-col {
    width: 100%;
  }
  #answer {
    width: 100%;
    height: 170px;
  }
  .calculate-body {
    padding: 10px 35px 40px;
  }
  .subtext {
    line-height: 20px;
  }
  input[type="date"] {
    max-width: unset;
    margin: 10px 0px;
  }
  .left-col h3 {
    margin-bottom: 10px;
  }
  .btn {
    padding: 15px 25px;
  }
}
